import React , { Component } from 'react';
import './LoginPage.scss';

import { connect } from 'react-redux';
import ALoginUser from '../../store/actions/ALoginUser';

class LoginPage extends React.Component {
    constructor ( props ) {
        super ( props );
        this.LP_clickHaandler = this.LP_clickHaandler.bind( this ); 
    }
    LP_clickHaandler () {
        this.props.LP_tryToLogin();
    }
    render() {
        return (
            <div className = "LoginPage">
                <input 
                    type = "submit"
                    value = "login"
                    onClick = { this.LP_clickHaandler }></input>
            </div>
        )
    }
} 

let mapState = state => {
    return {

    }
}

let mapDispatch = dispatch => {
    return {
        LP_tryToLogin : () =>dispatch( ALoginUser() )
    }
}

export default connect(mapState, mapDispatch) (LoginPage);