import React , { Component } from 'react';
import './BigMenu.scss';

import BigMenuCard from './BigMenuCard/BigMenuCard';
import Card_Anim from '../Cards/Card_Anim';
import Card_Games from '../Cards/Card_Games';
import Card_Mish from '../Cards/Card_Mish';
import Card_Napr from '../Cards/Card_Napr';
import Card_Strat from '../Cards/Card_Strat';
import Card_Tests from '../Cards/Card_Tests';
import Card_Tvor from '../Cards/Card_Tvor';
 
class BigMenu extends React.Component {
    constructor ( props ) {
        super ( props ) ;
        this.state = {
            curMenu : [
                { 
                    "title" : "Напрацоўкі",
                    "img"   : "",
                    "comp"  : <Card_Napr/> 
                },
                { 
                    "title" : "Гульні",
                    "img"   : "",
                    "comp"  : <Card_Games/> 
                },
                { 
                    "title" : "Анімацыі",
                    "img"   : "",
                    "comp"  : <Card_Anim/> 
                },
                { 
                    "title" : "Тэсты",
                    "img"   : "",
                    "comp"  : <Card_Tests/> 
                },
                { 
                    "title" : "Творчасць",
                    "img"   : "",
                    "comp"  : <Card_Tvor/>
                },
                { 
                    "title" : "Стратэгія",
                    "img"   : "",
                    "comp"  : <Card_Strat/> 
                },
                { 
                    "title" : "Мысленне",
                    "img"   : "",
                    "comp"  : <Card_Mish/> 
                }
            ],
            curShowArr : [
                
            ],
            curShowIndex : 0
        }
        this.slideLeft = this.slideLeft.bind( this );
        this.slideRight = this.slideRight.bind ( this );      
    }
    componentDidMount() {
        this.setState((state) => ({
            ...state,
            curShowArr : [ state.curMenu[0],state.curMenu[1],state.curMenu[2],state.curMenu[3] ]  
        }))
    }
    slideLeft () {
        if ( this.state.curShowIndex > 0 ) {
            let ind = this.state.curShowIndex -1;
            this.setState((state) => ({
                ...state,
                curShowIndex : state.curShowIndex - 1,
                curShowArr : [ state.curMenu[ind],state.curMenu[ind+1],state.curMenu[ind+2],state.curMenu[ind+3] ]  
            }))
        }
    }
    slideRight () {
        if ( this.state.curShowIndex < this.state.curMenu.length - 4) {
            let ind = this.state.curShowIndex +1 ;
            this.setState((state) => ({
                ...state,
                curShowIndex : state.curShowIndex + 1,
                curShowArr : [ state.curMenu[ind],state.curMenu[ind+1],state.curMenu[ind+2],state.curMenu[ind+3] ]  
            }))
        }
    }
   
    render() {
        return (
            <div className = "BigMenu">
                <input  
                    type = "submit"
                    value = "<"
                    onClick = { this.slideLeft }
                    className = "BM_slider__btn__left"></input>
                <input 
                    type = "submit"
                    value = ">"
                    onClick = { this.slideRight}
                    className = "BM_slider__btn__right"></input>
               { this.state.curShowArr.map (el => (
                   <BigMenuCard 
                    header ={el.title}
                    /> 
               ))}
            </div>
        )
    }
}

export default BigMenu;