import React , { Component } from 'react';
import './BigMenuCard.scss';

import { connect } from 'react-redux';
import AShowCard from '../../../../store/actions/AShowCard';

class BigMenuCard extends React.Component {
    constructor ( props ) {
        super ( props ) ;
        this.cardClickHander = this.cardClickHander.bind( this );
    }
cardClickHander ( xx ) {    
    this.props.showCard( xx )
}
    render() {
        return (
            <div 
                className = "BigMenuCard"
                onClick = { (xx) => this.cardClickHander( this.props.header ) }>
                <p className = "BMC_header">{this.props.header}</p>
            </div>
        )
    }
}
let mapState = state => {
    return {}
}

let mapDispatch = dispatch => {
    return {
        showCard:(xx)=>dispatch( AShowCard ( xx ))
    }
}
export default connect (mapState, mapDispatch ) (BigMenuCard);