import React, { Component } from 'react';
import './MainPage.scss';
import BigMenu from './BigMenu/BigMenu';

import Card_Tests from '../../components/MainPage/Cards/Card_Tests';
import Card_Anim  from '../../components/MainPage/Cards/Card_Anim';
import Card_Games from '../../components/MainPage/Cards/Card_Games';
import Card_Mish  from '../../components/MainPage/Cards/Card_Mish';
import Card_Napr  from '../../components/MainPage/Cards/Card_Napr';
import Card_Strat from '../../components/MainPage/Cards/Card_Strat';
import Card_Tvor  from '../../components/MainPage/Cards/Card_Tvor';

import { connect } from 'react-redux';
import AShowCard  from '../../store/actions/AShowCard';

class MainPage extends React.Component {
    constructor ( props ) {
        super ( props );
        this.setState = {
            curShowedPage : <BigMenu/>
        }
        this.cardClickHander = this.cardClickHander.bind( this );
    }
    static getDerivedStateFromProps(nextProps, state) {
        let curComp = <BigMenu />;
        switch ( nextProps.curSubPage ) {      
          case "Тэсты"      : curComp = <Card_Tests />; break;
          case "Напрацоўкі" : curComp = <Card_Napr />; break;
          case "Гульні"     : curComp = <Card_Games />; break;
          case "Анімацыі"   : curComp = <Card_Anim />; break;
          case "Творчасць"  : curComp = <Card_Tvor />; break;
          case "Стратэгія"  : curComp = <Card_Strat />; break;
          case "Мысленне"   : curComp = <Card_Mish />; break;
          default : curComp = <BigMenu />;
        }
        return {
          ...state,
          curShowedPage : curComp
        };
      }
    cardClickHander ( xx ) {    
        this.props.showCard( xx )
    }
    render() {
        return (
            <div class = "MainPage">
              <div className = "MainNav">
                    <ul className = "HeadNav">
                        <li onClick = { (xx) => this.cardClickHander( "Напрацоўкі" ) }>Напрацоўкі</li>
                        <li onClick = { (xx) => this.cardClickHander( "Гульні" ) }>Гульні</li>
                        <li onClick = { (xx) => this.cardClickHander( "Анімацыі" ) }>Анімацыі</li>
                        <li onClick = { (xx) => this.cardClickHander( "Тэсты" ) }>Тэсты</li>
                        <li onClick = { (xx) => this.cardClickHander( "Творчасць" ) }>Творчасць</li>
                        <li onClick = { (xx) => this.cardClickHander( "Стратэгія" ) }>Стратэгія</li>
                        <li onClick = { (xx) => this.cardClickHander( "Мысленне" ) }>Мысленне</li>                       
                    </ul>
              </div>              
              <div className = "MainPage_showedPage">
                  { this.state.curShowedPage }
              </div>
              <div className = "FootNav">
                  <p className = "FootNavHeader">Карысныя спасылкі</p>
                    <ul className = "FootNavList">
                        <li>Літаратурная палічка</li>
                        <li>Літаратурная палічка</li>   
                        <li>Літаратурная палічка</li>
                        <li>Літаратурная палічка</li> 
                        <li>Літаратурная палічка</li>                 
                        
                    </ul>
              </div>
            </div>
        )
    }
}

let mapState = state => {
    return {
        curSubPage : state.curSubPage
    }
}

let mapDispatch = dispatch => {
    return {
        showCard:(xx)=>dispatch( AShowCard ( xx ))
    }
}
export default connect (mapState, mapDispatch ) (MainPage);